@import url(https://fonts.googleapis.com/css2?family=Roboto);
* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   list-style: none;
   font-family: "Roboto", sans-serif;
}

body {
   background-color: rgba(29, 32, 37, 1);
}

::-webkit-scrollbar {
   background-color: rgba(29, 32, 37, 1);
   width: 15px;
}

::-webkit-scrollbar-thumb {
   background-color: rgba(255, 255, 255, 0.5);
}

::-webkit-scrollbar-button {
   display: none;
}
.container {
   width: 100%;
   padding: 2.8rem;
}

.btn {
   border: none;
   border-radius: 4px;
   padding: 14px 22px;
   margin-right: 8px;
   letter-spacing: 1px;
   font-weight: 700;
   color: white;
}

.btn-outline {
   border: 2px solid rgb(58, 56, 51);
   background-color: transparent;
}

.btn-filled {
   border: 2px solid rgb(58, 56, 51);
   background-color: rgb(29, 28, 25);
}

.row {
   padding: 10px;
   color: white;
   margin-top: 20px;
   position: relative;
}

.row__header {
   margin-bottom: 10px;
   font-size: 18px;
   text-transform: uppercase;
}

.posters {
   display: flex;
   object-fit: contain;
   overflow-y: hidden;
   overflow-x: scroll;
   scroll-behavior: smooth;
}

.posters::-webkit-scrollbar {
   display: none;
}

.poster img {
   height: 250px;
   border-radius: 4px;
   margin-right: 10px;
   transition: all 0.3s;
   /* Add loading gif */
}

.poster__large {
   position: relative;
}
.poster__large img {
   width: 430px;
   height: 95%;
   border-radius: 4px;
   margin-right: 8px;
   z-index: 1;
   /* Add loading gif */
}

.poster__gradient {
   position: absolute;
   z-index: 10;
   height: 50%;
   bottom: 170px;
   width: 430px;
   /* background: linear-gradient(
      180deg,
      rgba(37, 37, 37, 0),
      rgba(37, 37, 37, 0.3),
      rgba(37, 37, 37, 0.6),
      rgba(37, 37, 37, 0.9),
      rgba(37, 37, 37, 1)
   ); */
}

.poster__content {
   z-index: 15;
   color: white;
   position: relative;
   width: 430px;
   height: 170px;
   margin-top: -170px;
   display: flex;
   flex-direction: column;
   align-items: left;
   background-color: rgba(37, 37, 37, 1);
   border-radius: 0px 0px 4px 4px;
}

.poster__content p {
   font-size: 1rem;
   letter-spacing: 2px;
   text-transform: uppercase;
   margin-bottom: 5px;
   /* margin-top: -25px; */
   padding-left: 1.5rem;
   padding-top: 1.5rem;
}

.poster__content h2 {
   font-size: 18px;
   font-weight: 700;
   padding-left: 1.5rem;
}

.play__button {
   width: 100%;
   position: absolute;
   bottom: 0;
   height: 80px;
   background-color: blue;
   border-radius: 0px 0px 4px 4px;
   display: flex;
   align-items: center;
   cursor: pointer;
}
.play__button button {
   margin-right: 10px;
   margin-left: 10px;
   color: white;
   background-color: black;
   border: none;
   height: 40px;
   width: 40px;
   border-radius: 50%;
}

.play__button button:focus {
   outline: none;
}

.play__button button i {
   font-size: 1.1rem;
   margin-left: 3px;
   margin-top: 2px;
}

/* RowLong styling */

.row__long {
   border-radius: 4px;
   height: 780px;
   position: relative;
   margin-top: 50px;
}

.row_long_content {
   position: absolute;
   z-index: 10;
   bottom: 0;
   left: 0;
   padding: 3rem;
}

.row_long_content h4 {
   font-size: 0.7rem;
   margin-bottom: 10px;
}

.row_long_content h1 {
   margin-bottom: 10px;
   letter-spacing: 1px;
   font-size: 3rem;
}

.row_long_content p {
   max-width: 500px;
   margin-bottom: 20px;
   line-height: 1.5;
   font-size: 1.1rem;
}

.row_long_gradient {
   border-radius: 4px;
   position: absolute;
   left: 0;
   height: 100%;
   width: 100%;
   margin-top: -10px;
}

/* Row Buttons */
.button__left,
.button__right {
   position: absolute;
   z-index: 100;
   top: 50%;
   transform: translateY(-50%);
   height: 50px;
   width: 50px;
   border-radius: 50%;
   border: none;
   cursor: pointer;
   background-color: rgb(34, 47, 62);
   color: white;
   font-size: 1.3rem;
   /* margin-left: -20px; */
}

.button__left:focus,
.button__right:focus {
   outline: none;
}

.button__left i,
.button__right i {
   margin-top: 3px;
}

.button__right {
   right: 0;
   margin-right: -25px;
}

.button__left {
   margin-left: -25px;
}

.header {
   width: 100%;
   height: 830px;
   position: relative;
}

.header__gradient {
   height: 100%;
   width: 100%;
   position: absolute;
   top: 0;
   left: 0;
}

.header__content {
   padding: 4rem 3rem;
   position: absolute;
   color: white;
   display: flex;
   flex-direction: column;
   z-index: 10;
   bottom: 0;
   left: 0;
}

.header__content h1 {
   font-size: 4rem;
   margin-bottom: 15px;
}

.header__content p {
   max-width: 600px;
   margin-bottom: 20px;
   font-size: 1.1rem;
   line-height: 1.5;
}

nav {
   width: 100%;
   position: fixed;
   z-index: 100;
   display: flex;
   align-items: stretch;
   justify-content: space-between;
   padding: 1rem;
   background: linear-gradient(0deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.9));
   transition: all 0.4s ease;
}

.navbar__filled {
   background-color: black;
}

.nav__logo {
   height: 25px;
   margin-top: 6px;
   padding-left: 30px;
}

.nav__items {
   display: flex;
   position: relative;
   color: #fff;
}

.nav__item {
   margin-right: 16px;
   padding: 10px 14px;
   border-radius: 4px;
   transition: all 0.3s ease-in;
   text-align: center;
   cursor: pointer;
   font-weight: 600;
}

.nav__item a {
   color: white;
   text-decoration: none;
}

.nav__item:hover {
   background-color: rgb(36, 39, 46);
}

.user_items {
   color: white;
   margin-top: 8px;
   margin-right: 20px;
}

.user_items i {
   font-size: 1.3rem;
   cursor: pointer;
}

.user_items i:not(:last-child) {
   margin-right: 30px;
}

